@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap);
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'Roboto', sans-serif;;
}
body {
  margin: 0;
  font-size: 16px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #1a1a1a;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.w-100 {
  width: 100%;
}
.container {
  width: 100%;
  max-width: 1140px;
  margin-left: auto;
  margin-right: auto;   
}
.row {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
}
.col-4 {
  flex-grow: 0;
  flex-shrink: 0;
  flex-basis: calc(100% / 3);
  padding-left: 15px;
  padding-right: 15px;
}
.col-3 {
  flex-grow: 0;
  flex-shrink: 0;
  flex-basis: calc(100% / 4);
  padding-left: 15px;
  padding-right: 15px;
}
/* Header */
.header {
    border-bottom: 1px solid #fd7337;
}
.header-wrap {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px 15px;

    color: white;
}
.header .header-title {
    padding: 5px 0;
    font-size: 18px;
    font-weight: 700;
}
.header .header-search {
    display: flex;
    max-width: 500px;
    width: 100%;
    margin: 0 auto;

    border-radius: 6px;
    font-size: 18px;
    overflow: hidden;
}
.header .btn-search {
    display: block;
    padding: 0.5rem 0.75rem;
    outline: none;
    border: none;

    font-size: inherit;
    font-weight: 500;
    color: inherit;
    color: #999999;
    
    border-left: 1px solid #999;
    background: #403f3f;
    cursor: pointer;
    transition: opacity 0.3s;
}
.header .btn-search:hover {
    opacity: 0.75;
}
.header .header-search input {
    flex-grow: 1;
    padding: 0.5rem 0.75rem;
    background-color: #403f3f;
    
    border: none;
    outline: none;
    font-size: inherit;
    color: #999999;
}
.header .header-search input:focus,
.header .header-search input:-webkit-autofill {
    background-color: #403f3f !important;
}
/* cocktail-list */
.cocktail-list {
    color: #fff;
    padding: 15px 0;
}
.cocktail-list .list-wrap > :nth-child(n) {
    margin-bottom: 20px;
}
.cocktail-list h1 {
    font-size: 22px;
    margin-bottom: 30px;
    text-align: center;
}
.cocktail-list .cocktail-item {
    display: flex;
    flex-direction: column;
    height: 100%;
    border-radius: 6px;
    overflow: hidden;
    background-color: #403f3f;
}
.cocktail-list .cocktail-item .cocktail-img {
    flex-shrink: 0;
}

.cocktail-list .cocktail-detail {
    padding: 12px 16px;
    flex-grow: 1;
}
.cocktail-list .cocktail-detail h2 {
    margin-bottom: 10px;
    font-size: 17px;
}
.cocktail-list .cocktail-detail p {
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    overflow: hidden;
}
